@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    font-family: 'Lato', 'Helvetica Neue', sans-serif;
    background-color: rgb(0, 0, 10);
}

/*Personalizar Scroll*/
::-webkit-scrollbar {
    background: rgb(0, 33, 58);
    width: 10px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 10px #00000070;
}

::-webkit-scrollbar-thumb {
    background: #7dcd40;
}

::-webkit-scrollbar-thumb:hover {
    background: rgb(175, 255, 29);
}

h1 {
    font-family: 'Anton', sans-serif;
}

a.link-unstyled {
    color: inherit;
}

a.link-unstyled:hover {
    color: inherit;
    text-decoration: none;
}

.btn {
    padding: 0.375rem 2rem;
}

.btn.btn-primary {
    background-color: rgb(175, 255, 29);
    border-color: rgb(175, 255, 29);
    color: rgb(0, 0, 0);
    font-weight: bold;
    transition: all 0.2s;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover,
.btn.btn-primary:not(:disabled):not(.disabled):active,
.btn.btn-primary:not(:disabled):not(.disabled):focus {
    transform: scale(1.1);
}

.btn.btn-danger {
    background-color: rgb(255, 29, 29);
    border-color: rgb(255, 29, 29);
    color: rgb(255, 255, 255);
    transition: all 0.2s;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover,
.btn.btn-danger:not(:disabled):not(.disabled):active,
.btn.btn-danger:not(:disabled):not(.disabled):focus {
    transform: scale(1.1);
}

.color-white {
    color: #f4f4f7 !important;
}

.swal-title {
    color: rgb(255, 255, 255) !important;
}

.swal-button-text {
    color: rgb(0, 0, 0) !important;
    background-color: rgb(175, 255, 29) !important;
    padding: 10px 20px !important;
}

.swal-text {
    color: rgb(255, 255, 255) !important;
}
