.Badges__hero {
    width: 100%;
    padding: 2rem 0;
    background: url('../../images/stars.svg'), rgb(0, 0, 0);
    background-repeat: repeat;
    margin-bottom: 1rem;
}

.Badges__hero>.Badges__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Badges__container {
    width: 100%;
    max-width: 580px;
    margin: 0 auto;
    padding: 0 1rem;
}

.Badges_conf-logo {
    margin-bottom: 2rem;
}

.Badges__buttons {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
}